import React, {useState} from 'react'
import Affairs from './Affairs'

// types
export type AffairPriorityType = 'high' | 'middle' | 'low'
export type AffairType = {
    _id: number,
    name: string,
    priority: string
}
export type FilterType = 'all' | AffairPriorityType

// constants
const defaultAffairs: Array<AffairType> = [
    {_id: 1, name: 'Wake up the human', priority: 'high'},
    {_id: 2, name: 'Groom yourself', priority: 'low'},
    {_id: 3, name: 'Practice ninja moves', priority: 'low'},
    {_id: 4, name: 'Eat - as much as possible', priority: 'high'},
    {_id: 5, name: 'Run like a psycopath', priority: 'middle'},
]

// pure helper functions
export const filterAffairs = (affairs: Array<AffairType>, filter: FilterType): Array<AffairType> => {
    if (filter === 'high') {
        return affairs.filter(a => a.priority === 'high')
    } else if (filter === 'low') {
        return affairs.filter(a => a.priority === 'low')
    } else if (filter === 'middle') {
        return affairs.filter(a => a.priority === 'middle')
    } else {
        return affairs
    }
}
export const deleteAffair = (affairs: Array<AffairType>, affairId: number): Array<AffairType> => {
    return affairs.filter(a => a._id !== affairId)
}

function HW2() {
    const [affairs, setAffairs] = useState<Array<AffairType>>(defaultAffairs)
    const [filter, setFilter] = useState<FilterType>('all')

    const filteredAffairs = filterAffairs(affairs, filter)
    const deleteAffairCallback = (_id: number) => setAffairs(deleteAffair(affairs, _id))

    return (
        <div>
            <hr/>
            homeworks 2

            {/*should work (должно работать)*/}
            <Affairs
                data={filteredAffairs}
                setFilter={setFilter}
                deleteAffairCallback={deleteAffairCallback}
            />

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeAffairs/>*/}
            <hr/>
        </div>
    )
}

export default HW2
